body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html {
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }
  
  body {
    margin: 0;
    font: 100 14px 'Roboto';
  }
  
  button {
    display: block;
    background: none;
    border: none;
    padding: 0;
    font-family: inherit;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    outline: none;
    
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  
  button:active {
    box-shadow: inset 0px 0px 80px 0px rgba(0,0,0,0.25);
  }
  
  #wrapper {
    height: 100vh;
    
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  #app {
    width: 320px;
    height: 530px;
    position: relative;
  }
  
  .calculator {
    width: 100%;
    height: 100%;
    background: black;
    
    display: flex;
    flex-direction: column;
  }
  
  #wrapper .calculator {
    box-shadow: 0px 0px 20px 0px #aaa;
  }
  
  .calculator-display {
    color: white;
    background: #1c191c;
    line-height: 130px;
    font-size: 6em;
    
    flex: 1 1;
  }
  
  .auto-scaling-text {
    display: inline-block;
  }
  
  .calculator-display .auto-scaling-text {
    padding: 0 30px;
    position: absolute;
    right: 0;
    -webkit-transform-origin: right;
            transform-origin: right;
  }
  
  .calculator-keypad {
    height: 400px;
    
    display: flex;
  }
  
  .calculator .input-keys {
    width: 240px;
  }
  
  .calculator .function-keys {
    display: flex;
  }
  
  .calculator .digit-keys {
    background: #e0e0e7;
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap-reverse;
  }
  
  .calculator-key {
    width: 80px;
    height: 80px;
    border-top: 1px solid #777;
    border-right: 1px solid #666;  
    text-align: center;
    line-height: 80px;
  }
  .calculator .function-keys .calculator-key {
    font-size: 2em;
  }
  .calculator .function-keys .key-multiply {
    line-height: 50px;
  }
  .calculator .digit-keys .calculator-key {
    font-size: 2.25em;
  }
  .calculator .digit-keys .key-0 {
    width: 160px;
    text-align: left;
    padding-left: 32px;
  }
  .calculator .digit-keys .key-dot {
    padding-top: 1em;
    font-size: 0.75em;
  }
  .calculator .operator-keys .calculator-key {
    color: white;
    border-right: 0;
    font-size: 3em;
  }
  
  .calculator .function-keys {
    background: -webkit-linear-gradient(top, rgba(202,202,204,1) 0%, rgba(196,194,204,1) 100%);
    background: linear-gradient(to bottom, rgba(202,202,204,1) 0%, rgba(196,194,204,1) 100%);
  }
  .calculator .operator-keys {
    background:  -webkit-linear-gradient(top, rgba(252,156,23,1) 0%, rgba(247,126,27,1) 100%);
    background:  linear-gradient(to bottom, rgba(252,156,23,1) 0%, rgba(247,126,27,1) 100%);
  }
